/**
 * @description
 *
 * ServiÃ§o responsÃ¡vel por emitir as notificaÃ§Ãµes em uma pÃ¡gina. SÃ£o disponibilizados os mÃ©todos de:
 *
 * - success,
 * - warning,
 * - error,
 * - information.
 *
 * Cada um destes mÃ©todos recebe como parÃ¢metro o objeto "ThfNotification" que contÃ©m os dados da mensagem e o
 * objeto ViewContainerRef que Ã© a representaÃ§Ã£o do container do componente onde serÃ¡ criada a notificaÃ§Ã£o.
 *
 * Estas notificaÃ§Ãµes serÃ£o exibidas durante 10 segundos, apÃ³s este tempo a mesma Ã© removida automaticamente.
 */
export abstract class ThfNotificationBaseService {

   // Array responsÃ¡vel por guardar a instÃ¢ncia de thf-toaster's superiores.
  stackTop: Array<ComponentRef<any>> = [];

  // Array responsÃ¡vel por guardar a instÃ¢ncia de thf-toaster's inferiores.
  stackBottom: Array<ComponentRef<any>> = [];

  // DuraÃ§Ã£o do toaster ativo.
  protected durationToaster = 10000;

  /**
   * Emite uma notificaÃ§Ã£o de sucesso.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificaÃ§Ã£o ou somente a string com a mensagem da notificaÃ§Ã£o.
   */
  public success(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Success));
  }

  /**
   * Emite uma notificaÃ§Ã£o de atenÃ§Ã£o.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificaÃ§Ã£o ou somente a string com a mensagem da notificaÃ§Ã£o
   */
  public warning(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Warning));
  }

  /**
   * Emite uma notificaÃ§Ã£o de erro.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificaÃ§Ã£o ou somente a string com a mensagem da notificaÃ§Ã£o
   */
  public error(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Error));
  }

  /**
   * Emite uma notificaÃ§Ã£o de informaÃ§Ã£o.
   *
   * @param notification {ThfNotification | string} Objeto com os dados da notificaÃ§Ã£o ou somente a string com a mensagem da notificaÃ§Ã£o
   */
  public information(notification: ThfNotification | string) {
    this.createToaster(this.buildToaster(notification, ThfToasterType.Information));
  }

  /**
   * @docsPrivate
   *
   * Cria um objeto do tipo ThfToaster de acordo o tipo.
   *
   * @param notification {ThfNotification | string} Objeto ThfNotification com os dados da notificaÃ§Ã£o
   */
  private buildToaster(notification: ThfNotification | string, type: ThfToasterType): ThfToaster {
    let index = 0;
    let orientation;

    if ((<ThfNotification>notification).orientation === undefined ||
        (<ThfNotification>notification).orientation === ThfToasterOrientation.Bottom) {
      index = this.stackBottom.length;
      orientation = ThfToasterOrientation.Bottom;
    } else {
      index = this.stackTop.length;
      orientation = ThfToasterOrientation.Top;
    }

    const toaster: ThfToaster = {
      componentRef: undefined,
      message: (<ThfNotification>notification).message || <string>notification,
      type: type,
      orientation: orientation,
      action: (<ThfNotification>notification).action,
      actionLabel: (<ThfNotification>notification).actionLabel,
      position: index
    };

    if ((<ThfNotification>notification).action) {
      toaster.action = (param: any) => {
        (<ThfNotification>notification).action();
        this.destroyToaster(param.componentRef);
      };
    }

    return toaster;
  }

  /**
   * @docsPrivate
   *
   * MÃ©todo responsÃ¡vel por criar o thf-toaster.
   *
   * @param toaster {ThfToaster} Objeto contendo as informaÃ§Ãµes do toaster.
   */
  abstract createToaster(toaster: ThfToaster): void;

  /**
   * @docsPrivate
   *
   * MÃ©todo responsÃ¡vel por destruir o thf-toaster.
   *
   * @param toaster {ComponentRef} NÃºmero da posiÃ§Ã£o ou instancia do toaster a ser destruÃ­do.
   * @param orientation {ThfToasterOrientation} OrientaÃ§Ã£o do ThfToaster: Top ou Bottom
   */
  abstract destroyToaster(toaster: ComponentRef<any>): void;

}
